
    .form-outer{
        position: relative;
        padding: 24px 0;
        max-width: 1440px;
        margin: 0 auto;
        @media screen and (min-width: 767px) {
            padding: 48px 0;
        }
    }
    .form-container {
        flex-direction: column;
        width: 100%;
        //margin: 32px auto;
        background-color: #fff;
        padding: 0;
        border: 1px solid #EEF1F3;
        &.post-form {
            max-width: 760px;
            margin: 1rem auto;
        }
        .img-area {
            @media screen and (min-width: 1024px) {
                margin-bottom: 32px;
            }
                svg {
                max-width: calc(100% - 32px);
            }
        }
        @media screen and (min-width: 1024px) {
            flex-direction: row;
            padding: 32px 48px 0 16px;
            .img-area {
                margin-bottom: 0;
                flex-basis: 40%;
                min-width: 40%;
                position: relative;
                bottom: -10px;
                
            }
        }
    }
        .form {
            flex-direction: column;
            margin: 16px;
            @include body-mobile;
            @media screen and (min-width: 767px) {
                margin-left: 32px;
                margin-bottom: 32px;
                @include body;
            }
            
            >div, form {
                margin: 24px 0;
            }
            
            .form-fields {
                margin-bottom: 24px;
                display:flex;
                flex-direction: column; 
                @media screen and (min-width: 767px) {
                    flex-direction: row;
                }
                input {
                    width: calc(100% - 32px);
                    margin-bottom: 16px;
                    @media screen and (min-width: 767px) {
                        width: 50%;
                    }
                    &:first-of-type {
                        margin-right: 16px;
                    }
                    &:focus {
                        border-color: $turquoise;
                    }
                    &::placeholder {
                        color: $grey;
                    }
                    
                }
                
            .button {
                &.large {
                    width: calc(100% - 32px);
                    @media screen and (min-width: 767px) {
                        width: 100%;
                    }
                }
            }
            }

            
        }
    

    .form-disclaimer {
        @include small-text;
    }
    #svg-outer {
        position:relative;
    }
    #form-svg {
        
        position: absolute;
        left: -10%;
        width: 118%;
        height: 100%;
        bottom: -50px;
      
        z-index: 0;
        img {
            width: 100%;
            position: absolute;
            bottom: 0;
        }
        @media screen and (max-width: 1024px) {
            display: none;
        }
    }

    .team-form {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        @media screen and (min-width: 767px) {
            flex-direction: row;
        }
        .form-fields-left {
            @media screen and (min-width: 676px) {
                flex-basis: 50%;
                max-width: 50%;
                padding: 16px;
            }   
        }
        .form-fields-right   {
            @media screen and (min-width: 676px) {
                flex-basis: 50%;
                max-width: 50%;
                padding: 16px;
            }
            
            textarea {
                height: calc(100% - 32px);
                &:focus {
                    border-color: $turquoise;
                }
            }
        }
        .form-input {
            flex-basis: 100%;
            max-width: 100%;
        }
        input, textarea {
            margin: 16px 0;
            @include body-mobile;
            &:focus {
                border-color: $turquoise;
            }
            &.button {
                width: 100%;
            }
        }
       
    }

    .form-container {
        &.team-form-container {
            padding: 48px;
            .form {
                margin: 0;
                width: 100%;
                h3, .form-intro {
                    max-width: 480px;
                    margin: 16px auto;
                    text-align: center;
                }
            }
            
        }
    }

    // register interest form

    #popmake-1727 {
        h2 {
           margin-top: 16px; 
        }
        button.wpforms-submit {
           min-width: 240px;
        }
        .pum-close {
            color: transparent;
            text-shadow: none;
        }

        .wpforms-container {
            max-width: 640px;
            margin-left: 0;
        }
    }

    #interest-form {
        .form-fields {
            display: block;
            .wpforms-field-container {
                display: flex;
                flex-wrap: wrap;
                .wpforms-field {
                    width: 49%;
                    margin: 5px 0;
                    @media screen and (max-width: 550px) {
                        width: 100%;
                    }
                    &:first-of-type {
                        margin-right: 2%;
                        width: 49%;
                        @media screen and (max-width: 550px) {
                            width: 100%;
                            margin-right: 0;
                        }
                    }
                    &#wpforms-1726-field_6-container {
                        width: 100%;
                    }
                    &.wpforms-field-checkbox  {
                        width: 100%;
                    }
                    
                    input[type='checkbox'] {
                        width: auto;
                    }
                    input {
                        &:focus {
                            border-color: $turquoise;
                        }
                    }
                }
            }
            button {
                background-color: $orange;
                width: 100%;
                height: 56px;
                font-size: 16px;
                font-weight: 400;
                border-radius: 28px;
                box-shadow: 2px 4px 8px rgba(0,0,0,.3);
                color: #fff;
            }
        }
    }

    
    